.cell{
  width: 100%;
  height: 2rem;
  display: flex;
  padding: 0.3rem 0;

  img{
    width: 1.5rem;
    height: 1.5rem;
    display: block;
    margin: 0 0.3rem;
  }
  .msg{
    height: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h4{
      font-size: 16px;
    }

    >p{
      font-size: 12px;
      color: #666666;
      height: 12px;
      width: 3.6rem;
      line-height: 12px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .sell{
      font-size: 12px;
      color: #666666;

      span{
        &:nth-child(1){
          margin-right: 0.2rem;
        }
      }
    }

    .price{
      display: flex;
      justify-content: space-between;
      span{
        color: red;
        font-size: 18px;
      }

      b{
        font-size: 12px;
        color: #999999;
        font-weight: normal;
      }
      .btn{
        width: 0.4rem;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        margin-left:1.2rem ;
        background: #ffb000;
        border-radius: 100%;
      }
    }
  }
}