.cell{
  height: 1.2rem;
  display: flex;
  padding: 0.2rem;
  .imgbox{
    width: 1.52rem;
    height: 1.14rem;
    padding-right: 0.1rem;

    img{
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .msg{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h4{
      font-size: 16px;
      font-weight: normal;
      color: #333;
    }
    .c{
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      span{
        margin-left: 0.05rem;
      }
      p{
        margin-left: 0.2rem;
        margin-right: 0.4rem;
      }
    }
    .btm{
      display: flex;
      font-size: 12px;
      p{
        margin-left: 2rem;
        display: block;
        width: 1rem;
        height: 0.3rem;
        background: #ffca4a;
        border-radius: 0.05rem;
      }
    }
  }
}