
.home{
  padding-top: 1rem;
  padding-bottom: 1rem;

  .top{
    width: 94%;
    padding: 0 3%;
    height: 1rem;
    background: black;
    position: fixed;
    top: 0;
    z-index: 1;
    color: #fff;
    display: flex;
    align-items: center;
    //定位
    .pst{
      span{
        margin: 0 0.2rem 0 0.1rem;
      }
    }
    //搜索框
    .search{
      background: #fff;
      height: 0.6rem;
      position: absolute;
      right: 3%;
      border-radius: 0.3rem;
      display: flex;
      align-items: center;
      padding: 0 0.2rem;

      i{
        color: #333;
      }
      input{
        background: #fff;
        width: 2.5rem;
      }
    }
  }
}