.top{
  height: 1rem;
  background: #2E2F3B;

  span{
    color: #ccc;
    font-size: 28px;
    line-height: 1rem;
    margin-left: 0.2rem;
  }
}
.msg{
  height: 1.6rem;
  background: #2E2F3B;
  display: flex;
  color: #fff;
  font-size: 12px;
  padding: 0.1rem 0 0 0.2rem;

  img{
    width: 1.7rem;
    height: 1.28rem;
    display: block;
    margin-right: 0.1rem;
  }
  .right{
    height: 1.28rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .t{
      display: flex;
      justify-content: space-between;

      >span{
        background: #ffca4a;
        border-radius: 0.1rem;
        color: #333;
        display: flex;
        align-items: center;
      }
    }

    >p{
      width: 5.4rem;
      height: 0.24rem;
      line-height: 0.24rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;

    }

    .btm{
      display: flex;
      justify-content: space-between;
    }
  }
}
main{
  height: 9.5rem;
}

footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 1.2rem;
  background:#3B3B3C ;
  display:flex;
  align-items: center;
  justify-content: space-between;
  color: #999999;

  .imgbox{
    width: 1rem;
    height: 1rem;
    border-radius: 100%;
    overflow: hidden;
    position: absolute;
    left: 0.3rem;
    top: -0.2rem;

    img{
      width: 100%;
      height: 100%;
      display: block;
    }
  }

  span{
    margin-left: 1.5rem;
  }
  .charge{
    height: 1rem;
    width: 2rem;
    line-height: 1rem;
    text-align: center;
  }
}