.top{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 3.3rem;
  background: gold;

  .imgbox{
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 100%;
    overflow: hidden;
    border: 3px solid yellow;

    img{
      width: 100%;
      height: 100%;
      display: block;
    }
  }
   >P{
    margin-top: 0.1rem;
    font-size: 16px;
  }
}

.phone{
  background: #fff;
  margin-top: 0.3rem;
  height: 0.72rem;
  line-height: 0.72rem;
  text-align: center;
  color: #ffb000;
}
.time{
  text-align: center;
  margin-top: 0.2rem;
  font-size: 12px;
}